
.container{

}
.navbar{
  margin-bottom: 0px;
  min-height: 0px;
  font-size: 15px;
  font-weight: normal;
  border-radius: 0px;
  border-top: 0px;
  border-right: 0px;
}

#lang-dropdown,#curr-dropdown{
  padding-bottom: 5px;
  padding-top: 5px;

}
.navbar-default .navbar-nav>.active>a{
    background-color: #fff;
}
#navbar-form{
  padding-left: 200px;
}

#search-input {
  width:300px;
}
#mid-nav{
}

@media (min-width: 767px){
  #top-cart,#top-account{
      display: none;
      padding-top: 0px;
  }
}

@media (max-width: 767px){
  #mid-nav{
    display: none;
  }
}
@media (max-width: 992px){
  #navbar-form{
    padding-left:50px;
  }
}
@media (max-width: 767px){
  #navbar-form{
    float: right;
    margin-right: 15px;
  }
  #search-input{
    padding-left: 10px;
    width: 400px;
  }
}
@media (max-width: 425px){
  #navbar-form{
    padding-left: 20px;
  }
  #search-input{
    width: 150px;
  }
}
.navbar-form{
  border: 0px;
  display: -webkit-box;
  margin-top: 0px;
  margin-bottom: 0px;
  padding: 10px 0px 10px 10px;
}

#submit-btn{
  margin-left: 2px;
}



